body {
    margin: 5px;
}

div.polaroid {
    width: 100%;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-bottom: 25px;
}

div.polatext {
    text-align: left;
    word-break: keep-all;
    padding: 10px 20px;

}

.pola-image {
    width: 90%
}


 .dropdown {
     position: relative;
     display: inline-block;
 }

 .dropmenu {
     list-style-type: none;
     margin: 0;
     padding: 0;
     top: 45px;
     right: 0px;
     width: 200px;
     background-color: white;
     font-weight: bold;
     position: absolute;
     text-align: left;

     box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
     z-index: 1;
 }

 .droplink a {
     color: #000;
     text-decoration: none;
 }

 .droplink {
     padding: 8px 16px;
     border-bottom: 1px solid #e5e5e5;
 }

 .droplink:last-child {
     border-bottom: none;
 }

 .droplink:hover {
     background-color: #e5e5e5;
     color: white;
 }

 .button {
     width: 178px;
     height: 18px;
     background-color: #ff3232;
     padding: 12px;
     border-radius: 5px;
     font-weight: bold;
     color: white;
 }

 .button:before {
     content: "";
     position: absolute;
     width: 0px;
     height: 0px;
     border: 10px solid;
     border-color: white transparent transparent transparent;
     right: 6px;
     top: 18px;
 }
